var _cu = {
	
	getFnName(fn){
	    if(typeof fn !== "function") return;
	    var sName = (fn.name) ? fn.name : fn.toString().match(/function\s+([^(\(|\s)]+)/)[1];
	    return sName;
	},

	sendSimpleAjax(url, fnCallback, sData, method, aHeaders, sQuery) {
		let xhr = new XMLHttpRequest();
		xhr.open(method, url);

		if(aHeaders && this.isArray(aHeaders)) {
			aHeaders.forEach( (v) => {
				xhr.setRequestHeader(v[0], v[1]);
			});
		}

		xhr.addEventListener("load", function() {
			if (xhr.status === 200) {
				var sResult = JSON.parse(xhr.responseText);
				if(fnCallback && typeof fnCallback === 'function') fnCallback.call(this, sResult);
			}
		}.bind(this));
		xhr.send(sData);
	},

	sendSimpleJSONP(sURL, query, sCompletionName, fnCallback) {

		window[sCompletionName] = function(htData) {
			fnCallback(htData);
		};

		let encodedQuery = encodeURIComponent(query);

		var elScript = document.createElement('script');
		elScript.setAttribute('src', sURL + 'method=' + sCompletionName + '&q=' + encodedQuery);
		document.getElementsByTagName('head')[0].appendChild(elScript);

		elScript.onload= function(evt) {
			let callbackValue = window[sCompletionName];
			if(callbackValue && typeof callbackValue !== 'function') {
				fnCallback(callbackValue);
			}

			document.getElementsByTagName('head')[0].removeChild(this);
			window[sCompletionName] = null;
		}
	},

	runAnimation(nWidthForAnimation, nDuration, htFn) {
		if(htFn && htFn.before && (typeof htFn.before === "function")) { 
		 	htFn['before'].call();
		}

		this.bAnimationing = true;

		let sTF = this.getCSSName("transform");

		let elTarget = this.elTarget;
		let nStartTime = Date.now();

		let nPreviousTranslateX = this.getTranslate3dX(elTarget);


		function execAnimation() {

			let nNowTime = Date.now();
			let nDiffTime = nNowTime-nStartTime;

			if(nDiffTime > nDuration) {

				let nStep = nPreviousTranslateX + (nWidthForAnimation);
				elTarget.style[sTF] = 'translate3d(' + (nStep) + 'px, 0, 0)';
				this.bAnimationing =false;
				if(htFn && htFn.after && (typeof htFn.after === "function")) { 
					htFn['after'].call();
				}
				return;

			} else { 

				let nStep = nPreviousTranslateX + (nDiffTime / nDuration * nWidthForAnimation);
				elTarget.style[sTF] = 'translate3d(' + (nStep) + 'px, 0, 0)';
				window.requestAnimationFrame(execAnimation.bind(this));   

			}
		}

		window.requestAnimationFrame(execAnimation.bind(this));
	},

	setTranslate3dX(ele, nValue) {

		let sTF = this.getCSSName('transform');
		this.elTarget.style[sTF] = 'translate3d(' + (nValue) + 'px, 0, 0)';

	},

	getWidth(ele) {
		let nWidth = 0;
		if (ele.getBoundingClientRect().width) {
 			nWidth = ele.getBoundingClientRect().width;
		} else {
		  	nWidth = ele.offsetWidth;
		}

		return nWidth
	},

	getCSSName(sName) {
		if(this.htCacheData[sName]) return this.htCacheData[sName];

		var _htNameSet = {
						'transition' : ['webkitTransition', 'transition' ], 
						'transform' : ['webkitTransform', 'transform' ], 
				  }

		let aNameList = _htNameSet[sName];

		if(!this.isExist(aNameList)) return null;

		for(let i=0, len=aNameList.length; i < len ; i++) {
			if(typeof document.body.style[aNameList[i]] === 'string') this.htCacheData[sName] = aNameList[i];
			return this.htCacheData[sName];
		}
	},

	getTranslate3dX(ele) {
		let sTF = this.getCSSName("transform");
		let sPreCss = ele.style[sTF];
		let nPreX = +sPreCss.replace(/translate3d\((-*\d+(?:\.\d+)*)(px)*\,.+\)/g , "$1");
		return nPreX;
	},

	getCSSTransitionEnd() {
		let sTS = this.getCSSName('transition');
		let sTSE = (sTS === "webkitTransition") ? "webkitTransitionEnd" : "transitionend";
		return sTSE;
	},

	setCSS(el,style,value) {
		el.style[style] = value;
	},

	showLayer(el) {
		el.style.display = "block";
	},

	closeLayer(el) {
		el.style.display = "none";
	},

	//check null or undefined
	isExist(data){
		return data != null;
	},

	isArray(_a) {
		if (!Array.isArray) {
			return Object.prototype.toString.call(_a) === '[object Array]';
		}
		return Array.isArray(_a);
	},

	isFunction(fn) {
  		return Object.prototype.toString.call(fn) === '[object Function]';
	}
}
/**
 * @nigayo. SKPlanet.
 * @v0.0.3
 * @UIComponent common component
 */

class CommonComponent {
	constructor(htOption) {
		this.htOption = htOption;
		this.htCacheData = {};
	}

	setOption (htValue, htDefaultValue, htStorage) {
		Object.keys(htDefaultValue).forEach((v) => {
			if(typeof htValue[v] === "undefined") {
				htStorage[v] = htDefaultValue[v];
			} else {
                if(Object.prototype.toString.call(htValue[v]) !== "[object Object]") {
                	htStorage[v] = htValue[v];
                	return;
                }
                htStorage[v] = {};
				this.setOption.call(this, htValue[v], htDefaultValue[v],htStorage[v]); 
			}
		});
	}

	getDefaultCallbackList(aFn) {
		let htFn = {};
		aFn.forEach((v) => {
			htFn[v] = function(){};
		})
		return htFn;
	}

	initPlugins(aMyPluginName, aPluginList, elTarget) {
		let htPluginInstance = {};
		let oParent = this;
		aPluginList.forEach((v) => {
			let sName = v.name;
			if(aMyPluginName.indexOf(sName) < 0) return;
			htPluginInstance[sName] = new window[v.name](elTarget, v.option);
			htPluginInstance[sName].onUserMethod(v.useMethod);
			this._injectParentObject(oParent, htPluginInstance[sName]);
		});
		return htPluginInstance;
	}

	onMethodSuper(htFn) {
		Object.keys(htFn).forEach((v) => {
			if(typeof this.htPluginInstance[v2].htDefaultFn[v] !== "undefined") {
				htPluginFunction[v] = htFn[v];
			}
		});
	}

	_injectParentObject(oParent, oChild) {
		oChild.dockingPluginMethod(oParent);
	}
}


/**
 * @nigayo. SKPlanet.
 * @v0.0.3
 * @UIComponent RecentWordPlugin 
 */

class RecentWordPlugin extends CommonComponent {

	constructor(elTarget, htOption) {
		super(htOption);
		this.elTarget = elTarget;
		this.init(htOption);
	}

	init(htOption) {
		this.setInitValue();
		super.setOption(htOption, this.htDefaultOption, this.option);
		this.registerEvents();
		this.oStorage = new RecentWordPluginLocalStorageAddOn("searchQuery", this.option.maxList);

	}

	setInitValue() {
		let htDefaultFn = ['FN_AFTER_INSERT_RECENT_WORD', 'FN_AFTER_SELECT_RECENT_WORD'];
		this.htDefaultOption = {
			'usage' : true,
            'maxList' : 5
        }

		this.elRecentWordLayer 		= this.elTarget.querySelector(".recent-word-wrap");
		this.elClearRecentWordBtn 	= this.elTarget.querySelector(".deleteWord");
		this.elCloseButtonRWL		= this.elRecentWordLayer.querySelector(".closeLayer");
		this.elRecentULWrap			= this.elRecentWordLayer.querySelector(".ul-wrap");

		this.htDefaultFn 			= super.getDefaultCallbackList(htDefaultFn);
		this.htFn 					= {};
		this.option 				= {};
	}

	onUserMethod(htFn) {
		super.setOption(htFn, this.htDefaultFn, this.htFn);
	}

	registerEvents() {
		this.elClearRecentWordBtn.addEventListener("touchend", 	(evt) => { this.handlerClearRecentWord(evt)});	
		this.elCloseButtonRWL.addEventListener("touchend", 		(evt) => { this.handlerCloseLayer(evt)});
		this.elRecentULWrap.addEventListener("touchstart", 		(evt) => { this.handlerSelectRecentWordTouchStart(evt)});
		this.elRecentULWrap.addEventListener("touchend", 		(evt) => { this.handlerSelectRecentWordTouchEnd(evt)});
	}

	handlerClearRecentWord(evt) {
		this.oStorage.removeKeywords();
		this.elRecentWordLayer.querySelector("ul").innerHTML = "";
		this.elClearRecentWordBtn.style.display = "none";
	}

	//TODO. duplicate
	handlerCloseLayer(evt) {
		this.elRecentWordLayer.style.display = "none";
	}

	handlerSelectRecentWordTouchStart(evt) {
		this.htTouchStartSelectedWord = {'x' : evt.changedTouches[0].pageX, 'y' : evt.changedTouches[0].pageY};
	}

	handlerSelectRecentWordTouchEnd(evt) {
		let nowPageY = evt.changedTouches[0].pageY;
		if(this.isExecuteTouchScroll(nowPageY)) return;
		this.htFn['FN_AFTER_SELECT_RECENT_WORD'](evt.target);
	}

	isExecuteTouchScroll(pageY) {
		var nDiff = this.htTouchStartSelectedWord.y - pageY;
		if(nDiff !== 0) return true;
		return false;
	}

	saveQuery(sQuery) {
		this.oStorage.saveKeyword(sQuery);
	}

	showRecentSearchWord() {
		let sData = this.oStorage.getKeywords();
		if(sData === null || sData === "") return;
		this.elRecentWordLayer.style.display = "block";
		this.elClearRecentWordBtn.style.display = "block";
		let aData = JSON.parse(sData);
		this.htFn['FN_AFTER_INSERT_RECENT_WORD'](aData, this.option.maxList);
	}

	dockingPluginMethod(oParent) {
		oParent.onPluginMethod({
			'FN_AFTER_FOCUS' 	: this.showRecentSearchWord.bind(this),
			'FN_AFTER_INPUT'	: this.handlerCloseLayer.bind(this),
			'FN_AFTER_SUBMIT'	: this.saveQuery.bind(this)
		});
	}


}
/**
 * @nigayo. SKPlanet.
 * @RecentWordPluginLocalStorageAddOn - dependency:RecentWordPlugin. 
 */

class  RecentWordPluginLocalStorageAddOn {
	
	constructor(sKey, nMaxList) {
		this.sKey = sKey;
		this.nMaxList = nMaxList;
	}

	saveKeyword(sKeyword) {
		if(this.validStorage()) return;

		let aLegacy = this.getKeywords();

		//to Array
		if(aLegacy === null) aLegacy = []; 
		else aLegacy = JSON.parse(aLegacy);

		//save data 
		let nIndex = aLegacy.indexOf(sKeyword);
		if( nIndex > -1) {
			if(nIndex === (aLegacy.length-1)) return;
			aLegacy.splice(nIndex, 1);
		} 

		aLegacy.unshift(sKeyword);
		if(aLegacy.length >= this.nMaxList) aLegacy.length = this.nMaxList;
		localStorage.setItem(this.sKey, JSON.stringify(aLegacy));
	}

	getKeywords(){ 
		if(this.validStorage()) return;

		let sResult = localStorage.getItem(this.sKey);
		return sResult;
	}

	removeKeywords() {
		if(this.validStorage()) return;

		return localStorage.removeItem(this.sKey);
	}

	validStorage() {
		if( typeof(Storage) === "undefined") return;
	}

}
/**
 * @nigayo. SKPlanet.
 * @v0.0.4
 * @UIComponent smartsearch
 */

class SmartSearch extends CommonComponent {

	COMPONENT_CONFIG() {
		 this.COMPONENT_DATA = {
			ELEMENT_SELECTOR 	: {
				inputFieldWrap 		: ".inputWrap",
				inputField 			: ".input-field",
				autoCompleteWrap 	: ".auto-complete-wrap",
				closeLayer 			: ".closeLayer",
				clearQueryBtn 		: ".clearQuery",
				autoULWrap			: ".auto-complete-wrap .ul-wrap",
				realForm 			: "#search-form"
			} 
		}
	}


	constructor(elTarget, htOption) {
		super(htOption)
		this.elTarget = elTarget;
		this.init(htOption);
	}

	init(htOption) {
		this.setInitValue();
		super.setOption(htOption, this._htDefaultOption, this.option);
		this.registerEvents();
	}

	setInitValue() {
		let _el 				= this.elTarget;

		this.COMPONENT_CONFIG();

		let s = this.COMPONENT_DATA.ELEMENT_SELECTOR;

		//TODO. Separate data.
		let aDefaultFnName = [	
			'FN_AFTER_INSERT_AUTO_WORD',
			'FN_AFTER_SELECT_AUTO_WORD', 
			'FN_AFTER_FORM_SUBMIT',
			'FN_AFTER_FOCUS'
		];

		let aDefaultPluginFnName = [	
			'FN_AFTER_FOCUS',
			'FN_AFTER_INPUT',
			'FN_AFTER_SUBMIT'
		];

		this._htDefaultOption 	= {
			"requestType" 		: 'jsonp',
			"sAutoCompleteURL" 	: "",
			"jsonp_callbackName": ""
		}

		this.option 			= {};

		this.elInputFieldWrap	= _el.querySelector(s.inputFieldWrap);
		this.elInputField 		= _el.querySelector(s.inputField);
		this.elAutoCompleteLayer= _el.querySelector(s.autoCompleteWrap);
		this.elClearQueryBtn 	= _el.querySelector(s.clearQueryBtn);
		this.elForm 			= _el.querySelector(s.realForm);

		this.elCloseButton 		= this.elAutoCompleteLayer.querySelector(s.closeLayer);
		this.elAutoULWrap		= this.elAutoCompleteLayer.querySelector(s.autoULWrap);

		this.htCachedData 		= {};
		this.htDefaultFn 		= super.getDefaultCallbackList(aDefaultFnName);
		this.htDefaultPluginFn	= super.getDefaultCallbackList(aDefaultPluginFnName);

		this.htUserFn 			= {};
		this.htPluginFn 		= {};

		//plugins
		this.aMyPluginName 		= ['RecentWordPlugin'];
		this.htPluginInstance 	= {};
	}

	registerEvents() {
		this.elInputFieldWrap.addEventListener("touchend", 	(evt) => this.handlerInputWrap(evt));

		this.elInputField.addEventListener("keypress", 		(evt) => this.handlerInputKeyPress(evt));
		this.elInputField.addEventListener("keydown", 		(evt) => this.handlerInputKeydown(evt));
		this.elInputField.addEventListener("input", 		(evt) => this.handlerInputKeyInput(evt));

		this.elCloseButton.addEventListener("touchend", 	(evt) => this.handlerCloseLayer(evt));
		this.elClearQueryBtn.addEventListener("touchend", 	(evt) => this.handlerClearInputValue(evt));

		this.elAutoULWrap.addEventListener("touchstart", 	(evt) => this.handlerSelectAutoCompletedWordTouchStart(evt));
		this.elAutoULWrap.addEventListener("touchend", 		(evt) => this.handlerSelectAutoCompletedWordTouchEnd(evt));

		this.elForm.addEventListener("submit", 				(evt) => this.handlerSubmitForm(evt));
	}

	onUserMethod(htFn) {
		super.setOption(htFn, this.htDefaultFn, this.htUserFn);
	}

	onPluginMethod(htFn) {
		super.setOption(htFn, this.htDefaultPluginFn, this.htPluginFn);
	}

	onPlugins(aPluginList) {
		this.htPluginInstance = super.initPlugins(this.aMyPluginName, aPluginList,  this.elTarget);
	}

	/***** START EventHandler *****/
	handlerInputWrap(evt) {
		this.htUserFn['FN_AFTER_FOCUS']();
		this.htPluginFn['FN_AFTER_FOCUS']();
		this.elInputField.focus();
	}

	//if need something executing of inputed value
	handlerInputKeyPress(evt) {}
	
	//if need something executing of inputed special key value.(e.g. keycode 8: backspace)
	handlerInputKeydown(evt) {}

	handlerInputKeyInput(evt) {
		let sInputData = this.elInputField.value;

		if(sInputData.length > 0 ) _cu.setCSS(this.elClearQueryBtn, "display", "inline-block");
		else _cu.closeLayer(this.elClearQueryBtn);

		this.htPluginFn['FN_AFTER_INPUT']();

		if (typeof this.htCachedData[sInputData] === "undefined") this.autoCompleteRequestManager(sInputData);
		else this.execAfterAutoCompleteAjax(sInputData, this.htCachedData[sInputData]);
	}

	handlerClearInputValue(evt) {
		this.elInputField.value = "";
		this.handlerCloseLayer();
		_cu.closeLayer(this.elClearQueryBtn);
		//evt.stopPropagation();
	}
	
	handlerCloseLayer(evt) {
		_cu.closeLayer(this.elAutoCompleteLayer);
	}

	handlerSelectAutoCompletedWordTouchStart(evt) {
		this.htTouchStartSelectedWord = {'x' : evt.changedTouches[0].pageX, 'y' : evt.changedTouches[0].pageY};
	}

	handlerSelectAutoCompletedWordTouchEnd(evt) {
		let nowPageY = evt.changedTouches[0].pageY;
		if(this.isExecuteTouchScroll(nowPageY)) return;

		let sText = this.htUserFn['FN_AFTER_SELECT_AUTO_WORD'](evt.target);
	}

	handlerSubmitForm(evt, sQuery) {
        if(evt) evt.preventDefault();
        sQuery = sQuery || this.elInputField.value;
		this.htUserFn['FN_AFTER_FORM_SUBMIT'](sQuery);
		this.htPluginFn['FN_AFTER_SUBMIT'](sQuery);
	}
	/***** End EventHandler *****/


	isExecuteTouchScroll(pageY) {
		var nDiff = this.htTouchStartSelectedWord.y - pageY;
		if(nDiff !== 0) return true;
		return false;
	}

	execAfterAutoCompleteAjax(sQuery, sResult) {
		this.htUserFn['FN_AFTER_INSERT_AUTO_WORD'](sResult);
		if(this.elAutoCompleteLayer.querySelector("li") !== null) _cu.showLayer(this.elAutoCompleteLayer);
		else _cu.closeLayer(this.elAutoCompleteLayer);

		//save history
		this.htCachedData[sQuery] = sResult;
	}

	autoCompleteRequestManager(sQuery) {
		let type = this.option.requestType;
		let url = this.option.sAutoCompleteURL;
		switch(type) {
			case 'jsonp':
				this.makeAutoCompleteJSONPRequest(sQuery,url);
				break;
			case 'ajax':
				this.makeAutoCompleteAjaxRequest(sQuery,url);
				break;
			default: 
				//do something..
		}
	}

	makeAutoCompleteJSONPRequest(sQuery, sURL) {
		let sCallbackName = this.option.jsonp_callbackName;
		_cu.sendSimpleJSONP(sURL, sQuery, sCallbackName, this.execAfterAutoCompleteAjax.bind(this,sQuery));
	}

	//TODO. 
	makeAutoCompleteAjaxRequest(sQuery, sURL) {
		// hardcoded url for test.
		let url = "../jsonMock/"+ sQuery +".json";
		let aHeaders = [["Content-Type", "application/json"]];
		_cu.sendSimpleAjax(url, this.execAfterAutoCompleteAjax.bind(this, sQuery), 
			JSON.stringify({
				sQuery : sQuery,
				nTime : Date.now() 
			}), 
		"get", aHeaders, sQuery);
	}

}
